.step-controls {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    margin-top: auto;
    width: 100%;
    user-select: none;
}

.step-controls-title {
    font-size: 1.3rem;
    color: var(--text-primary);
    user-select: none;
}

.step-controls .step-number {
    margin: 0 1rem;
    font-size: 1.65rem;
}

.step-controls button.step-control {
    background: rgba(0, 0, 0, 0);
    color: var(--text-primary);
    border: none;
    padding: 4px;
    margin: 3px 6px 6px 6px;
    outline: none;
    font-size: 1.5rem;
}

.step-controls button.step-control:hover:enabled {
    cursor: pointer;
    color: #28d1b6;     /* placeholder */
}

.step-controls button.step-control:disabled {
    cursor: not-allowed;
}

@media screen and (max-width: 900px) {
    .step-controls {
        position: absolute;
        top: calc(100vh - 200px);
        right: 20px;
        width: unset;
    }

    .step-controls-title {
        position: absolute;
        top: calc(100vh - 220px);
        right: 55px;
    }
}